<template>
  <div class="personal">
    <PersonalHeader></PersonalHeader>
    <div class="greyBg">
      <div class="enterBox">
        <div class="title">
          <h2>企业入驻</h2>
        </div>
        <div class="enterContent">
          <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
            <el-form-item label="品牌名">
              <el-input v-model="formLabelAlign.brand" placeholder="请输入品牌名"></el-input>
            </el-form-item>
            <el-form-item label="公司名">
              <el-input v-model="formLabelAlign.company" placeholder="请输入公司名"></el-input>
            </el-form-item>
            <el-form-item label="姓名">
              <el-input v-model="formLabelAlign.name" placeholder="请输入您的姓名"></el-input>
            </el-form-item>
            <el-form-item label="所在城市">
              <el-input v-model="formLabelAlign.city" placeholder="请输入所在城市"></el-input>
            </el-form-item>
            <el-form-item label="手机号">
              <el-input v-model="formLabelAlign.phone" placeholder="请输入手机号码"></el-input>
            </el-form-item>
            <el-form-item label="图片验证">
              <el-input v-model="formLabelAlign.imgCode" placeholder="请输入图片验证码"></el-input>
              <img class="picYz" src="https://www.jiamengzhinan.com./uploads/20190625/201906255d1183edd60fc.png">
            </el-form-item>
            <el-form-item label="验证码">
              <el-input v-model="formLabelAlign.code" placeholder="请输入手机验证码"></el-input>
              <span class="messageBtn" v-if="showCode">获取验证码</span>
              <span class="messageBtn" v-else>{{outTime}}S后重新获取</span>
            </el-form-item>
            <el-button class="submitBtn" type="primary" @click="onSubmit">提交申请</el-button>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import api from "@/api/login-api"

  export default {
    name: "personal",
    data(){
      return {
        labelPosition: 'right',
        showCode:true,
        formLabelAlign: {
          brand: '',
          company: '',
          name: '',
          city:'',
          phone:'',
          imgCode:'',
          code:''
        }
      }
    },
    created(){

    },
    methods:{
      onSubmit() {
        this.$alert('企业入驻信息提交成功，稍后平台会与您联系', '', {
          confirmButtonText: '确定'
        });
      }
    },
    components: {
      PersonalHeader
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .greyBg{
    width: 100%;
    padding:20px 0 80px;
    background: #F4F5FA;
  }
  .enterBox{
    width: 1200px;
    height: 640px;
    margin: 0 auto;
    background: #fff;
    .title{
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      margin-bottom: 40px;
      h2{
        width: 66px;
        height: 58px;
        line-height: 58px;
        border-bottom: 2px solid #4F74F4;
        font-size: 16px;
        color: #333;
        text-align: center;
        margin-left: 33px;
      }
    }
    .enterContent{
      width: 400px;
      padding-left: 40px;
    }
    .el-form-item{
      position: relative;
      overflow: hidden;
    }
    .picYz{
      width: 105px;
      height: 38px;
      position: absolute;
      right: 1px;
      top: 1px;
      z-index: 10;
      cursor: pointer;
    }
    .messageBtn{
      width: 105px;
      height: 22px;
      line-height: 22px;
      border-left: 1px solid #E3E4E5;
      text-align: center;
      position: absolute;
      right: 0;
      top: 8px;
      color: #5389F7;
      cursor: pointer;
    }
    .submitBtn{
      margin: 20px 0 0 81px;
    }
  }




</style>



